* {
  color: white;
  margin: 0;
  padding: 0;
  text-decoration: none;
  font-family: tilt neon;
}

.App {
  text-align: center;
}

body {
  background-color: rgb(0, 0, 0);
}

.ppic {
  border-radius: 100px;
  width: 2cm;
  height: 2cm;
}

.profile-box {
  margin-top: 1cm;
  display: flex;
  justify-content: center;
}

.profile-box-container {
  justify-content: center;
  display: flex;
  column-gap: .2cm;
  align-items: center;
  width: fit-content;
  border: 2px solid #16161c;
  background-color: #0f0f13;
  padding: .6cm .8cm;
  border-radius: 10px;
}

.name-box {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

.user-name {
  font-size: 30px;
}

.user-name-para {
  font-size: 20px;
  color: rgb(98, 98, 98);
}

.AuthScreen {
  display: flex;
  justify-content: center;
  align-items: center;

}

.auth-box {
  margin-top: 3cm;
  width: 16cm;
  // border: 1px solid white;
  border-radius: 10px;
  background-color: #0f0f13;
  padding: .3cm .6cm;
}

input:focus {
  outline: none;
  border: 3px solid rgb(81, 81, 81);
}

#noUser {
  display: none;
}

.welcome {
  margin-bottom: -.4cm;
}

#usernameExist {
  display: none;
}

#whitespaceFound {
  display: none;
}

#twoDots {
  display: none;
}

#otherThanUnderscore {
  display: none;
}

#capitalUsername {
  display: none;
}

.error {
  font-size: 15px;
  color: rgb(252, 47, 47);
}

.tagline {
  font-size: 62px;
  line-height: 1.75cm;
  // font-weight: 600;
  color: rgb(243, 243, 243);
  font-family: Tilt Neon;
}

.sub-tagline {
  font-size: 20px;
  margin-top: .3cm;
  color: rgb(96, 96, 96);
  font-family: Tilt Neon;
}

.google-button {
  margin-top: 1.3cm;
  margin-bottom: .5cm;
  background-color: transparent;
  color: white;
  border: 3px solid #2d2d2d;
  padding: .3cm .7cm;
  font-family: Tilt Neon;
  border-radius: 10px;
  font-size: 18px;
  cursor: pointer;
  transition: .25s;

  &:hover {
    background-color: #2d2d2d;

  }
}

.Nav {
  padding: .3cm 10vw;
  background-color: #0f0f13;
}

.name {
  font-family: Tilt Neon;
  font-size: 30px;
  cursor: pointer;
}

.setPassword {
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  position: fixed;
  z-index: 10;
  display: flex;
  justify-content: center;

  backdrop-filter: blur(10px);


}

.saveU {
  width: 100%;
  background-color: transparent;
  color: white;
  border: 3px solid #2d2d2d;
  padding: .3cm .7cm;
  font-family: Tilt Neon;
  border-radius: 10px;
  font-size: 18px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  transition: .25s;

  &:hover {
    background-color: #2d2d2d;

  }

}

#setUsername {
  display: none;
}


.user-form {
  display: flex;
  row-gap: .3cm;
  flex-direction: column;
  align-items: center;
  width: 100%;
  justify-content: center;
}

.user-input {
  background-color: transparent;
  color: white;
  font-family: Tilt Neon;
  border: 3px solid #2d2d2d;
  font-size: 18px;
  padding: 0.3cm 0.3cm;
  /* Adjusted padding values */
  width: calc(100% - 0.8cm);
  /* Adjusted width to account for padding */
  border-radius: 10px;
}

.log-out-box {
  font-family: tilt neon;
  font-size: 16px;
  margin-top: .3cm;
  cursor: pointer;
  color: rgb(97, 97, 97);
  transition: .2s;

  &:hover {
    color: white;
  }
}

.username-box-head {
  width: 100%;

  display: flex;
  justify-content: space-between;
  align-items: center;
}

.username-box {
  display: flex;
  width: 10cm;
  margin-top: 3cm;
  height: fit-content;
  row-gap: .3cm;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #0f0f13;
  padding: .0cm .5cm .5cm .5cm;
  border-radius: 10px;
}

.search-input {
  width: calc(100% - 0.6cm);
  background-color: transparent;
  border: none;
  padding: .3cm .3cm .2cm .07cm;
  font-size: 18px;
  border-radius: 10px;


  &:focus {
    outline: none;
    border: none;
  }
}

.su {
  transition: .3s;
  cursor: pointer;

  &:hover {
    border: 2px solid white;
  }
}




.no-user-found-box {
  margin-top: .5cm;
}

.ghost {
  width: 6cm;
  height: 6cm;

}

.notext {
  color: #5f5f5f;
}

.searchedUsersList {
  margin-top: .5cm;
  display: flex;
  justify-content: center;
  column-gap: .4cm;
  row-gap: .4cm;
  flex-wrap: wrap;
}

.search-para {
  color: rgb(98, 98, 98);
}

.Search {
  padding: 0cm 10vw;
}

.noUser-box {
  background-color: #0f0f13;
  width: fit-content;
  padding: .2cm 1cm .4cm 1cm;
  border-radius: 10px;
  margin-top: 3cm;
  height: fit-content;
}

.noUserBox-head {
  display: flex;
  justify-content: flex-end;
  margin-bottom: .25cm;
}

.uh-oh {
  font-size: 30px;
}

.search-input-box {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 0.0cm .25cm .2cm .25cm;
  border-radius: 10px;
  border: 3px solid #2d2d2d;
}

.searchImg {
  width: .7cm;
  margin-top: .15cm;
  height: .7cm;
 opacity: .45;
  transform: rotate(20deg);
}

.uh-oh-sub {
  color: rgb(97, 97, 97);
}

.notUser {
  position: fixed;
  z-index: 100;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  backdrop-filter: blur(10px);
  display: flex;
  justify-content: center;
}

.search-box {
  width: 100%;
  display: flex;
  flex-direction: column;
  row-gap: .2cm;
  margin-top: 1cm;
  align-items: flex-start;
  // border: 1px solid white;
}


.menu-item {
  cursor: pointer;
  font-family: Tilt neon;
  font-size: 16px;
  color: rgb(98, 98, 98);
  transition: .2s;

  &:hover {
    color: white;
  }
}

.menu {
  display: flex;
  column-gap: .5cm;
  align-items: center;
}

.nav-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
}